.breadcrumb-nav .breadcrumb-item {
  transition: color 0.2s ease;
}
.breadcrumb-nav .breadcrumb-item:hover {
  color: #1d4ed8;
}
.breadcrumb-nav .navigation-button {
  transition: all 0.2s ease;
}
.breadcrumb-nav .navigation-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.breadcrumb-nav .navigation-button:not(:disabled):hover {
  background-color: #f3f4f6;
}
